


















































import Vue from 'vue';
import { apiListBalita, apiDeleteBalita } from '@/services/balita';

type IData = {
  headers: any[];
  items: any[];
  keyword: string;
}

const defData = () => ({
  headers: [
    {
      text: 'ID',
      value: 'id'
    },
    {
      text: 'Nama',
      value: 'nama'
    },
    {
      text: 'Umur',
      value: 'age'
    },
    {
      text: 'Berat Badan',
      value: 'beratBadan'
    },
    {
      text: 'Tinggi Badan',
      value: 'tinggiBadan'
    },
    {
      text: 'Jenis Kelamin',
      value: 'sex'
    },
    {
      text: '',
      value: 'action'
    }
  ],
  items: [],
  keyword: ''
});

export default Vue.extend({

  data: defData,

  computed: {
    filtered () {
      const keywordUpper = this.keyword.toUpperCase();
      return this.items.filter((it: string) => {
        return it.nama.toUpperCase().includes(keywordUpper);
      });
    }
  },

  methods: {

    async loadDataBalita() {
      this.items = await apiListBalita();
    },

    async onDelete(id: number) {
      try {
        await apiDeleteBalita(id);
        alert('Sukses menghapus data balita');
      } catch (err) {
        alert('Gagal menghapus data balita');
        console.log(err);
      } finally {
        this.loadDataBalita();
      }
    }

  },

  mounted() {
    this.loadDataBalita();
  }

});

